@import 'variables';

.glitch-text {
  line-height: 1;
  margin: 0;
  display: inline-block;
  z-index: 2;
  margin-bottom: 20px;
  @media screen and (max-width: 570px) {
    font-size: 25px;
  }
}

.layers {
  position: relative;
  &::before {
    content: attr(data-text);
    position: absolute;
    width: 110%;
    z-index: -1;
    top: 10px;
    left: 15px;
    color: $red-color;
  }
  &::after {
    content: attr(data-text);
    position: absolute;
    width: 110%;
    z-index: -1;
    top: 5px;
    left: -10px;
    color: $yellow-color;
  }
}

.single-path {
  clip-path: polygon(
    0% 12%,
    53% 12%,
    53% 26%,
    25% 26%,
    25% 86%,
    31% 86%,
    31% 0%,
    53% 0%,
    53% 84%,
    92% 84%,
    92% 82%,
    70% 82%,
    70% 29%,
    78% 29%,
    78% 65%,
    69% 65%,
    69% 66%,
    77% 66%,
    77% 45%,
    85% 45%,
    85% 26%,
    97% 26%,
    97% 28%,
    84% 28%,
    84% 34%,
    54% 34%,
    54% 89%,
    30% 89%,
    30% 58%,
    83% 58%,
    83% 5%,
    68% 5%,
    68% 36%,
    62% 36%,
    62% 1%,
    12% 1%,
    12% 34%,
    60% 34%,
    60% 57%,
    98% 57%,
    98% 83%,
    1% 83%,
    1% 53%,
    91% 53%,
    91% 84%,
    8% 84%,
    8% 83%,
    4% 83%
  );
}

.paths {
  animation: paths 5s step-end infinite;
}
.movement {
  position: relative;
  animation: movement 8s step-end infinite;
}
.opacity {
  animation: opacity 5s step-end infinite;
}
.font {
  animation: font 7s step-end infinite;
}

.glitch {
  span {
    animation: paths 5s step-end infinite;
  }
  &::before {
    animation: paths 5s step-end infinite, opacity 5s step-end infinite,
      font 8s step-end infinite, movement 10s step-end infinite;
  }
  &::after {
    animation: paths 5s step-end infinite, opacity 5s step-end infinite,
      font 7s step-end infinite, movement 8s step-end infinite;
  }
}

